/* 1296px */
@media (max-width: 81em) {
  .book-review-card {
    width: 100rem;
  }
}

/* 1104px */
@media (max-width: 69em) {
  .book-review-card {
    width: 70rem;
  }

  .button {
    width: 22rem;
  }

  .logo h1 {
    font-size: 6rem;
  }

  .logo-pic-space {
    width: 10rem;
    height: 7rem;
  }
}

/* 880px */
@media (max-width: 55em) {
  .logo {
    gap: 3rem;
  }

  .logo h1 {
    font-size: 4rem;
  }

  .logo-pic-space {
    width: 8rem;
    height: 6rem;
  }

  .logo::after {
    width: 60rem;
  }

  .content-space {
    padding-block: 6rem;
  }

  .book-review-card {
    width: 70rem;
  }

  .button {
    padding: 1.2rem;
    width: 18rem;

    border-style: solid;
    font-size: 1.8rem;
  }
}

/* 800px */
@media (max-width: 50em) {
  .logo {
    gap: 3rem;
  }

  .logo h1 {
    font-size: 4rem;
  }

  .logo-pic-space {
    width: 8rem;
    height: 6rem;
  }

  .logo::after {
    width: 60rem;
  }

  .content-space {
    padding-block: 6rem;
  }

  h2,
  .review-form-title {
    font-size: 3.2rem;
  }

  .book-review-card {
    width: 60rem;
    padding: 2.2rem;
  }

  .book-review-card h3 {
    font-size: 2.6rem;
  }

  .book-review-card p,
  form label {
    font-size: 2rem;
  }

  .button {
    padding: 1.2rem;
    width: 18rem;

    border-style: solid;
    font-size: 1.8rem;
  }

  .pagination button {
    padding: 1rem;
  }
}

/* 672px */
@media (max-width: 42em) {
  .logo {
    gap: 2rem;
    padding-block: 2rem;
  }

  .logo h1 {
    font-size: 3.2rem;
  }

  .logo-pic-space {
    width: 6rem;
    height: 4rem;
  }

  .logo::after {
    width: 45rem;
  }

  .content-space {
    padding-block: 3rem;
    gap: 2rem;
  }

  h2,
  .review-form-title {
    font-size: 2.6rem;
  }

  .book-review-card {
    width: 44rem;
    padding: 2.2rem;
  }

  .book-review-card h3 {
    font-size: 2.2rem;
  }

  .book-review-card p,
  form label {
    font-size: 1.6rem;
  }

  .form-add-book {
    padding: 2rem;
  }

  .form-add-book-content {
    gap: 1rem;
  }

  .button {
    padding: 1rem;
    width: 14rem;

    border-style: solid;
    font-size: 1.4rem;
  }

  .pagination button {
    padding: 1rem;
  }
}

/* 528px */
@media (max-width: 33em) {
  .logo {
    gap: 1.6rem;
    padding-block: 1.6rem;
  }

  .logo h1 {
    font-size: 3.2rem;
  }

  .logo-pic-space {
    width: 6rem;
    height: 4rem;
  }

  .logo::after {
    width: 40rem;
  }

  .google-button img {
    height: 80%;
    width: 80%;
  }

  .sign-in-text {
    font-size: 1.2rem;
  }

  .content-space {
    padding-block: 3rem;
    gap: 2rem;
  }

  h2,
  .review-form-title {
    font-size: 2.6rem;
  }

  .book-review-card {
    width: 35rem;
    padding: 2.2rem;
    gap: 1.2rem;
  }

  .book-review-card h3 {
    font-size: 2.2rem;
  }

  .book-review-card p,
  form label {
    font-size: 1.6rem;
  }

  .form-add-book {
    padding: 2rem;
    gap: 2rem;
  }

  .form-add-book-content {
    gap: 1rem;
  }

  .button {
    padding: 0.8rem;
    width: 10rem;

    border-style: solid;
    font-size: 1.2rem;
  }

  .pagination button {
    padding: 1rem;
  }
}

/* 464px */
@media (max-width: 29em) {
  .logo {
    gap: 1.1rem;
    padding-block: 1.6rem;
  }

  .logo h1 {
    font-size: 2.4rem;
  }

  .logo-pic-space {
    width: 4rem;
    height: 2rem;
  }

  .logo::after {
    width: 25rem;
  }

  .google-button img {
    height: 75%;
    width: 75%;
  }

  .sign-in-text {
    font-size: 1rem;
  }

  .content-space {
    padding-block: 2rem;
    gap: 1.6rem;
  }

  h2,
  .review-form-title {
    font-size: 2rem;
  }

  .book-review-card {
    width: 26rem;
    padding: 2rem;
    gap: 1.1rem;
  }

  .book-review-card h3 {
    font-size: 1.8rem;
  }

  .book-review-card p,
  form label {
    font-size: 1.4rem;
  }

  .form-add-book {
    max-width: 26rem;
    padding: 2.2rem;
    gap: 1.1rem;
  }

  .form-add-book-content {
    gap: 0.6rem;
    row-gap: 1rem;
  }

  .button {
    padding: 0.4rem;
    width: 8rem;

    border-style: solid;
    font-size: 1.2rem;
  }

  .pagination button {
    padding: 1rem;
    font-size: 1.4rem;
  }
}
