* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(245, 211, 216);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.button {
  padding: 2.4rem;
  width: 32rem;
  border-radius: 20px;
  border: 4px;
  border-style: solid;
  font-size: 2rem;
  background-color: white;
  border-color: rgb(207, 104, 200);
  cursor: pointer;
}

.button:hover,
.button:active,
.button:focus {
  background-color: rgb(207, 104, 200);
  border-color: white;
  color: white;
}

.google-button {
  background-color: rgb(245, 211, 216);
  border: none;
}

.button-space {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
  align-items: center;
}

.sign-in-text {
  font-size: 1.6rem;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6rem;
  padding-block: 3rem;
  position: relative;
}

.logo::after {
  content: "";
  width: 68rem;
  position: absolute;
  border-bottom: 4px solid rgb(207, 104, 200);
  bottom: 0;
}

.logo h1 {
  font-size: 8rem;
  font-family: "Lora";
}

.logo-pic-space {
  width: 14rem;
  height: 10rem;
}

.sweetbooks-logo {
  width: 100%;
  height: 100%;
}

.app {
  display: grid;
  width: 100%;
  height: 100vh;
  grid-template-rows: auto 1fr;
}

.content-space {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 4rem;
  font-family: "fira sans";
  padding-block: 10rem;
}

h2 {
  font-size: 3.8rem;
}

.form-section {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}

.form-add-book {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  max-width: 120rem;
  padding: 3.2rem;
  border: 8px;
  background-color: rgb(255, 255, 255);
  border-style: solid;
  border-color: rgb(212, 139, 207);
  border-radius: 12px;
}

.form-add-book-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: 2rem;
}

form label {
  font-size: 3.2rem;
}

.review-form-title {
  font-size: 4.2rem;
  font-weight: 600;
}

form input {
  height: 3.2rem;
  border: 0.001rem;
  /* box-shadow: inset 0 0 2px #c4c3c2; */
  border-style: solid;
  border-radius: 5px;
  border-color: rgb(188, 188, 188);
}

.form-add-book-content input:focus {
  border-style: solid;
  border-radius: 5px;
  border-color: rgb(54, 153, 219);
}

.review-input {
  height: 8rem;
  border-style: solid;
  font-family: "fira sans";
}

.book-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.book-review-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  width: 100rem;
  padding: 3.2rem;
  border: 8px;
  background-color: rgb(255, 255, 255);
  border-style: solid;
  border-color: rgb(212, 139, 207);
  border-radius: 12px;
}

.book-review-card h3 {
  font-size: 3.2rem;
}

.book-review-card p {
  align-self: flex-start;
  font-size: 2.8rem;
}

.review-bold {
  font-weight: 500;
}

.pagination {
  display: flex;
  gap: 1.2rem;
}

.pagination button {
  font-size: 2rem;
  padding: 1.4rem;
  background-color: white;
  border: 3px;
  border-style: solid;
  border-color: rgb(207, 104, 200);
  border-radius: 5px;
  cursor: pointer;
}

.pagination button.active {
  background-color: rgb(207, 104, 200);
  color: white;

  border: 3px;
  border-style: solid;
  border-color: white;
  border-radius: 5px;
}
